import React, {Component} from 'react';
import Projects from './Projects';
import SocialProfiles from './SocialProfiles';
import profile from './assets/images/profile.png'

class App extends Component {
    state = {displayBio: false};

    toggleDisplayBio = () => {
        this.setState({displayBio: !this.state.displayBio}); //never directly set the state, use setState
    }

    render(){

        return (
            <div>
                <img src={profile} alt='profile' className='profile'/>
                <h1>Hello!</h1>
                <p>My name is Michael, I am a software developer.</p>
                {
                    this.state.displayBio ? (
                        <div>
                            <p>I live in Burlington Ontario, and am a recent uOttawa Comp Sci grad.</p>
                            <p>My favourite programming languages are Java and JS.</p>
                            <p>I am currently training for a half marathon.</p>
                            <button onClick={this.toggleDisplayBio}>Show Less</button>
                        </div>
                    ) : (
                        <div>
                            <button onClick={this.toggleDisplayBio}>Read More</button>
                        </div>
                    )
                }
                <hr />
                <Projects />
                <hr />
                <SocialProfiles />            
            </div>
        )
    }
}

export default App;